import React from "react";
import "./headerLogo.css";
import { Link } from "react-router-dom";
const headerLogo = () => {
  return (
    <div className="header__logo">
      <Link to="/">
        <img className="header__logo--img" src="./Logo.svg" alt="ggwp" />
      </Link>
    </div>
  );
};

export default headerLogo;
