import React from "react";
import { BrowserRouter, Routes, Route } from "react-router-dom";
// import Auth from "../container/Auth/auth";
import Home from "../container/HomePage/Home";
import Blog from "../container/blogPage/blog";
import Trips from "../container/TripsPage/Trips";
import SharedLayout from "../container/SharedLayout/SharedLayout";
import About from "../container/aboutPage/about";
import Error from "../container/ErrorPage/error";
import Login from "../container/Auth/login/login";
import SinglePage from "../container/singlePage/SinglePage";
import AuthProvider from "../container/Auth/auth/AuthProvider";

const Root = () => (
  <div>
    <BrowserRouter>
      <Routes>
        <Route path="/" element={<SharedLayout />}>
          <Route index path="/" element={<Home />} />
          <Route
            path="/about"
            element={
              <AuthProvider>
                <About />
              </AuthProvider>
            }
          />
          <Route
            path="/trips"
            element={
              <AuthProvider>
                <Trips />
              </AuthProvider>
            }
          />
          <Route
            path="/blog"
            element={
              <AuthProvider>
                <Blog />
              </AuthProvider>
            }
          />
        </Route>

        <Route path="/login" element={<Login />} />
        <Route path="*" element={<Error />} />
        <Route
          path="heroMain/:id"
          element={
            <AuthProvider>
              <SinglePage />
            </AuthProvider>
          }
        />
      </Routes>
    </BrowserRouter>
  </div>
);

export default Root;
