import React, {
  createContext,
  useState,
  useCallback,
  useEffect,
  useContext,
} from "react";

const context = createContext();
const url = "https://thecocktaildb.com/api/json/v1/1/search.php?s=";

const ContextProvider = ({ children }) => {
  const [loading, setLoading] = useState(true);
  const [search, setSearch] = useState("");
  const [items, setItems] = useState([]);

  const getItems = useCallback(async () => {
    setLoading(true);
    try {
      const res = await fetch(`${url}${search}`);
      const data = await res.json();

      const { drinks } = data;
      if (drinks) {
        const newItem = drinks.map((item) => {
          const { idDrink, strDrinkThumb, strDrink } = item;
          return {
            id: idDrink,
            name: strDrink,
            image: strDrinkThumb,
          };
        });
        setItems(newItem);
      } else {
        setItems([]);
      }
      setLoading(false);
    } catch (error) {
      console.log(error);
      setLoading(false);
    }
  }, [search]);

  useEffect(() => {
    getItems();
  }, [search, getItems]);

  useEffect(() => {
    fetch("https://jsonplaceholder.typicode.com/users")
      .then((res) => res.json())
      .catch((err) => console.log(err));
  }, []);

  const [message, setMessage] = useState(false);

  return (
    <context.Provider
      value={{ items, search, setSearch, loading, message, setMessage }}
    >
      <context.Consumer>{() => children}</context.Consumer>
    </context.Provider>
  );
};
const useGlobalContext = () => {
  return useContext(context);
};
export { context, useGlobalContext };
export default ContextProvider;
