import React from "react";
import { Link } from "react-router-dom";
import "./footerNav.css";

const FooterNav = () => {
  return (
    <div className="footer__nav">
      <div className="footer__nav--box">
        <h2 className="footer__nav--box--title">Go travel</h2>
        <p className="footer__nav--box--p">Copyright © 2021 Go travel</p>
        <p className="footer__nav--box--p">All rights reserved</p>
        <div className="footer__nav--social">
          <a href="https://www.instagram.com/" target="_blank">
            <i class="bx bxl-instagram"></i>
          </a>
          <a href="https://www.telegram.com" target="_blank">
            <i class="bx bxl-telegram"></i>
          </a>
          <a href="https://www.twitter.com" target="_blank">
            <i class="bx bxl-twitter"></i>
          </a>
          <a href="https://www.youtube.com/watch?v=GNrdg3PzpJQ" target="_blank">
            <i class="bx bxl-youtube"></i>
          </a>
        </div>
      </div>
      <ul className="footer__nav--list">
        <li className="footer__nav--item--title">Company</li>
        <li className="footer__nav--item">
          <Link to="/" className="footer__nav--link">
            Home
          </Link>
        </li>
        <li className="footer__nav--item">
          <Link to="./about" className="footer__nav--link">
            About
          </Link>
        </li>
        <li className="footer__nav--item">
          <Link to="./trips" className="footer__nav--link">
            Trips
          </Link>
        </li>
        <li className="footer__nav--item">
          <Link to="./blog" className="footer__nav--link">
            Blog
          </Link>
        </li>
      </ul>
    </div>
  );
};

export default FooterNav;
