import React from "react";
import "./home.css";
import Loading from "../../components/loading/Loading";
import HeroMain from "../../components/hero/heroMain/heroMain";
import { useGlobalContext } from "../../context/context";
import HeroTitle from "../../components/hero/heroTitle/heroTitle";

const Home = () => {
  const { items, loading } = useGlobalContext();

  if (loading) {
    return <Loading />;
  }
  if (items.length < 1) {
    return (
      <h2 className="section__title">
        Ops, Sorry! No items matched your search criteria
      </h2>
    );
  }
  return (
    <div className="hero container">
      <section className="hero__main">
        <HeroTitle />
        <div className="hero__body">
          {items.map((item) => {
            return <HeroMain key={item.id} {...item} />;
          })}
        </div>
      </section>
    </div>
  );
};

export default Home;
