import React, { useState } from "react";
import "./headerBtn.css";
import { Link } from "react-router-dom";

const HeaderBtn = () => {
  const [change, setChange] = useState(localStorage.getItem("token"));
  // const
  return (
    <div className="header__btn">
      <div className={`LoggedBtn ${change ? "hide animation" : ""}`}>
        <Link to="./login">
          <button className="header__btn--login">Login</button>
        </Link>
      </div>
      <div className={`exitBtn ${!change ? "hide animation" : ""}`}>
        <h3 className="exitBtn__title">Hi, You are logged!</h3>
        <button
          onClick={(e) => setChange(localStorage.removeItem("token"))}
          className="modal--btn"
        >
          Logout
        </button>
      </div>
    </div>
  );
};

export default HeaderBtn;
