import React from "react";
import { Link } from "react-router-dom";
import "./heroMain.css";

const heroMain = ({ id, name, image }) => {
  return (
    <div className="hero">
      <section className="hero__section">
        <div className="hero__main">
          <img className="hero__main--img" alt="img" src={image} />
        </div>
        <h2 className="hero__main--title">{name}</h2>
        <Link to={`/heroMain/${id}`} className="hero__main--btn">
          Read more
        </Link>
      </section>
    </div>
  );
};

export default heroMain;
